.experience_card_container {

    display: grid;
    grid-template-columns: auto auto auto;
    width: 100%;
    justify-content: center;
    grid-row: 20px;
}

.experience_card_container :nth-child(odd) {
    grid-column: 3/ 7;

}

.experience_card_container :nth-child(even) {
    
    grid-column: 1/3;
    grid-row: 2;

    

}

.experience_card_container :nth-child(3) {
    grid-row: 3;
   
}



.experience_icon{
    color: #562BBF;
    font-size: 45px;
}
.experience_year{
    font-size: 16px;
}





